<template>
  <v-app>
    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col>
            <v-sheet min-height="70vh" rounded="lg">
              <v-card class="pu-10 px-10 mu-10" elevation="0">
                <v-card-title class="headline justify-space-between">
                  <v-btn
                    icon
                    large
                    @click="
                      () => {
                        this.$router.go(-1);
                      }
                    "
                  >
                    <v-icon large>mdi-arrow-left</v-icon>
                  </v-btn>

                  <h2></h2>
                  <v-btn disabled text><v-spacer></v-spacer></v-btn>
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row class="ma-8">
                      <v-col>
                        <v-text-field
                          placeholder=""
                          v-model="member.email"
                          required
                          clearable
                          :rules="[
                            v => !!v || ' ',
                            v =>
                              /.+@.+\..+/.test(v) || '  ',
                            v => v.length <= 50 || '  '
                          ]"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          type="password"
                          placeholder=""
                          v-model="member.pw"
                          required
                          clearable
                          :rules="[
                            v => !!v || '  ',
                            v =>
                              (v && v.length > 7 && v.length <= 20) ||
                              ' 8  20   '
                          ]"
                          @keypress.enter="onSubmit()"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" class="mt-10">
                        <v-btn color="primary" block @click="onSubmit()"
                          ></v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions class="pb-5 justify-center">
                  <div class="text-center">  ?</div>
                  <v-btn color="secondary" text @click="signup()">
                    
                  </v-btn>
                </v-card-actions>
                <v-card-actions class="pb-5 justify-center">
                  <div class="text-center"> ?</div>
                  <v-btn color="secondary" text @click="findpw()">
                     
                  </v-btn>
                </v-card-actions>
                <v-snackbar
                  centered
                  v-model="snackbar"
                  timeout="2000"
                  :color="color"
                >
                  {{ msg }}
                </v-snackbar>
              </v-card>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapActions } from "vuex";
const MemberStore = "MemberStore";

export default {
  data: () => {
    return {
      //
      member: {
        email: "",
        pw: ""
      },
      //  
      snackbar: "",
      msg: "",
      color: ""
    };
  },
  methods: {
    ...mapActions(MemberStore, ["Login"]),

    onSubmit() {
      // 
      if (this.member.email == "" || this.member.pw == "") {
        this.msg = " !";
        this.color = "warning";
        this.snackbar = true;
        return;
      }
      this.Login(this.member) //Login action  (member payload )
        .then(response => {
          this.msg = response.msg;
          this.color = response.color;
          this.snackbar = true;

          if (response.result && sessionStorage.getItem("access-token") != "") {
            this.$router.push({ name: "main" });
          } else {
            this.member.email = "";
            this.member.pw = "";
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    signup() {
      this.$router.push("/signup");
    },
    findpw() {
      this.$router.push("/findpw");
    }
  }
};
</script>

<style></style>
