import findpage from "../../views/Find";

// ExampleRouter  
const findRoute = [
  /*
            
        {
            path: address   path -> http://localhost:8080/{path}
            ! : path   
            name: router name
            component: routing component
            props:    props  true,   false
        }
        ex)
        {
            path: '/board/general',
            name: 'general',
            component: General,
            props: true,
        },
           
    */
  {
    // 
    path: "/find",
    name: "findpage",
    component: findpage,
    props: false
  }
];

export default findRoute;
