<template>
  <v-container>
    <div id="map" class="map"></div>
    <v-snackbar centered v-model="snackbar" timeout="2000" :color="color">
      {{ msg }}
    </v-snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

const FindStore = "FindStore";

export default {
  data() {
    return {
      snackbar: false,
      color: "",
      msg: ""
    };
  },
  mounted() {
    window.kakao && window.kakao.maps
      ? this.initMap()
      : this.addKakaoMapScript();
  },
  computed: {
    ...mapGetters(FindStore, ["getSuggestList"])
  },
  methods: {
    addKakaoMapScript() {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=a2ef75e84ec5b6042fe36d22f21ed028";
      document.head.appendChild(script);
    },
    initMap() {
      var container = document.getElementById("map"); //   DOM 

      var options = {
        //     

        center: new kakao.maps.LatLng(33.450701, 126.570667), // .
        level: 3 // (,  )
      };

      var map = new kakao.maps.Map(container, options); //    

      // HTML5 geolocation    
      if (navigator.geolocation) {
        // GeoLocation    
        navigator.geolocation.getCurrentPosition(function(position) {
          var lat = position.coords.latitude, // 
            lng = position.coords.longitude; // 

          new kakao.maps.LatLng(lat, lng); //    geolocation   
          //   
        });
      } else {
        alert(1);
      }

      //    
      var imageSrc =
        "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

      for (var i = 0; i < this.getSuggestList.length; i++) {
        //     
        var imageSize = new kakao.maps.Size(24, 35);

        //   
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

        var lat = this.getSuggestList[i].lat;
        var lng = this.getSuggestList[i].lng;
        //  
        var marker = new kakao.maps.Marker({
          map: map, //   
          position: new kakao.maps.LatLng(lat, lng), //   
          title: this.getSuggestList[i].name, //  ,     
          image: markerImage //  
        });
        //  
        var infowindow = new kakao.maps.InfoWindow({
          content: this.getSuggestList[i].name
        });
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          makeOutListener(infowindow)
        );

        marker.setMap(map);
      }
      //     
      function makeOverListener(map, marker, infowindow) {
        return function() {
          infowindow.open(map, marker);
        };
      }

      //     
      function makeOutListener(infowindow) {
        return function() {
          infowindow.close();
        };
      }
    }
  }
};
</script>

<style>
.map {
  width: 95%;
  height: 400px;
  margin: auto;
}
</style>
