import axios from "../../axios/axios-common";

const SpotInfoStore = {
  namespaced: true, //   
  state: {
    spot: {
      name: " ",
      latitude: "37.570425",
      longitude: "126.991316",
      address: "  3",
      bhour_list: [
        {
          etc: "1 1 12 31  ^^"
        }
      ],
      menu_list: [
        {
          menu: "",
          price: "6000"
        },
        {
          menu: "",
          price: "5000"
        }
      ],
      tel: "010-1234-1234",
      review_cnt: 1,
      type: 1
    },
    images: {},
    closespot: []
  },

  getters: {
    getSpot(state) {
      return state.spot;
    },
    getImage(state) {
      return state.images;
    },
    getCloseSpot(state) {
      return state.closespot;
    }
  },
  mutations: {
    setSpot(state, payload) {
      state.spot = payload;
    },
    setImage(state, payload) {
      state.images = payload;
    },
    setCloseSpot(state, payload) {
      state.closespot = payload;
    }
  },

  actions: {
    // 
    reqSpot(context, no) {
      return axios
        .get("/store/" + no)
        .then(response => {
          if (response.data.message == "success") {
            context.commit("setSpot", response.data.result);
            return {
              result: true,
              msg: "  "
            };
          } else {
            return {
              result: false,
              msg: ""
            };
          }
        })
        .catch(error => {
          console.log(error);
          return {
            result: false,
            msg: " "
          };
        });
    },

    // 
    reqTourSight(context, id) {
      return axios
        .get("/toursight/" + id)
        .then(response => {
          if (response.data.message == "success") {
            context.commit("setSpot", response.data.result[0]);
            context.commit("setImage", response.data.result[1]);
            return {
              result: true,
              msg: "  "
            };
          } else {
            return {
              result: false,
              msg: ""
            };
          }
        })
        .catch(error => {
          console.log(error);
          return {
            result: false,
            msg: " "
          };
        });
    },
    //  
    reqCloseSpot(context, info) {
      return axios
        .get(
          "/toursight/around/" + info.lat + "/" + info.lng + "/" + info.range
        )
        .then(response => {
          if (response.data.message == "success") {
            context.commit("setCloseSpot", response.data.result);
            return {
              result: true,
              msg: "  "
            };
          } else {
            return {
              result: false,
              msg: ""
            };
          }
        })
        .catch(error => {
          console.log(error);
          return {
            result: false,
            msg: " "
          };
        });
    }
  }
};

export default SpotInfoStore;
