import axios from "../../axios/axios-common";

const ThemeStore = {
  namespaced: true,
  state: {
    themelist: [
      {
        id: "1",
        name: "  ",
        img: ""
      },
      {
        id: "2",
        name: " ",
        img: ""
      },
      {
        id: "3",
        name: "12 ",
        img: ""
      },
      {
        id: "4",
        name: "",
        img: ""
      },
      {
        id: "5",
        name: "",
        img: ""
      }
    ],
    theme: {}
  },
  getters: {
    getThemeList(state) {
      return state.themelist;
    },
    getThemeInfo(state) {
      return state.theme;
    }
  },
  mutations: {
    setThemeList(state, payload) {
      state.themelist = payload;
    },
    setTheme(state, payload) {
      state.theme = payload;
    }
  },
  actions: {
    /*
      
    */
    // 
    reqThemeList(context) {
      return axios
        .get("/recommend/theme")
        .then(response => {
          if (response.status) {
            context.commit("setThemeList", response.recommend_theme_list);
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    // 
    reqTheme(context, themeid) {
      return axios
        .get("/recommend/theme" + themeid)
        .then(response => {
          if (response.status) {
            context.commit("setTheme", response.themeinfo);
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};

export default ThemeStore;
