import axios from "../../axios/axios-common";

const MemberStore = {
  namespaced: true, //   
  state: {
    isLogin: false, //  
    memberInfo: {
      user_id: "ssafy",
      email: "ssafy@ssafy.com",
      nickname: "",
      img: ""
    }
  },

  getters: {
    getIsLogined(state) {
      // 
      return state.isLogin;
    },
    getMemberInfo(state) {
      //  
      return state.memberInfo;
    }
  },
  mutations: {
    setIsLogined(state, isLogin) {
      // 
      state.isLogin = isLogin;
    },

    setMemberInfo(state, memberInfo) {
      state.memberInfo = memberInfo;
    },
    logout(state) {
      //   
      state.isLogin = false;
      state.memberInfo = {};
    }
  },

  actions: {
    /*
      Login Component
    */
    // 
    Login(context, member) {
      return axios
        .post("/login", {
          // 
          email: member.email,
          password: member.pw
        })
        .then(response => {
          //   
          if (response.data.message == "success") {
            //  
            let token = response.data["auth-token"];
            sessionStorage.setItem("access-token", token); // 

            context.commit("setIsLogined", true); //  true 
            context.commit("setMemberInfo", response.data.result); // 

            return {
              result: true,
              msg: member.email + " ",
              color: "success"
            };
          } else {
            return {
              result: false,
              msg: " .",
              color: "warning"
            };
          }
        })
        .catch(error => {
          if (error.response.status === 401) {
            if (error.response.data.data === "email")
              return {
                result: false,
                msg: "   ",
                color: "warning"
              };
            else if (error.response.data.data === "fail")
              return {
                result: false,
                msg: "  ",
                color: "warning"
              };
          } else {
            console.log(error);
            return {
              result: false,
              msg: "  ",
              color: "warning"
            };
          }
        });
    },
    // 
    Logout(context) {
      context.commit("logout");
      sessionStorage.removeItem("access-token");
    },
    /*
      Signup Component
    */
    // 
    reqCheckEmail(context, email) {
      return axios
        .get("/user/email/" + email)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "success") {
            return true;
          }
        })
        .catch(error => {
          //  
          if (error.response.status === 409) {
            return false;
          } else console.log(error);
        });
    },

    // 
    reqCheckNickname(context, nickname) {
      return axios
        .get("/user/nickname/" + nickname)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "success") {
            return true;
          }
        })
        .catch(error => {
          //  
          if (error.response.status === 409) {
            return false;
          } else console.log(error);
        });
    },

    // 
    reqSignup(context, info) {
      return axios
        .post("/user", {
          email: info.email,
          nickname: info.nickname,
          pwd: info.pwd,
          gender: info.gender,
          birth: info.birth
        })
        .then(response => {
          console.log(response.data);
          if (response.data.message == "success") {
            return true;
          }
        })
        .catch(error => {
          //  
          if (error.response.status === 409) {
            return false;
          } else console.log(error);
        });
    },
    //   
    reqSignupValidation(context, email) {
      return axios
        .post("/signup/validation", email)
        .then(response => {
          if (response.data.message == "success") return true;
          return false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //    (   )
    reqClickEmailLink(context, info) {
      return axios
        .post("/signup/validated", {
          email: info.email,
          authkey: info.authkey
        })
        .then(response => {
          if (response.data.message == "success") return true;
          return false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    /*
      FindPw Component
    */

    //  
    reqFindPw(context, info) {
      return axios
        .post("/user/pw", {
          email: info.email,
          nickname: info.nickname
        })
        .then(response => {
          if (response.data.message == "success") {
            //  
            return {
              result: true,
              msg: "  "
            };
          }

          //   x
          return { result: false, msg: "  " };
        })
        .catch(error => {
          console.log(error);
        });
    },

    reqMemberInfo(context, user_id) {
      return axios
        .get("/user/" + user_id)
        .then(response => {
          if (response.data.message == "success") {
            context.commit("setMemberInfo", response.data.result);
            return { result: true, msg: " " };
          }
          return { result: false, msg: "  " };
        })
        .catch(error => {
          console.log(error);
        });
    },

    // 
    reqModifyMember(context, member) {
      return axios
        .put("/user/update", {
          nickname: member.nickname,
          password: member.pw,
          user_id: member.id
        })
        .then(response => {
          if (response.data.message == "success") return true;
          else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    reqRemoveMember(context, member_id) {
      return axios
        .delete("/user/" + member_id)
        .then(response => {
          if (response.data.message == "success") {
            context.commit("logout");
            sessionStorage.removeItem("access-token");
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
          return false;
        });
    }
  }
};

export default MemberStore;
