<template>
  <v-app>
    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col>
            <v-sheet min-height="70vh" rounded="lg">
              <v-card class="pu-10 px-10 mu-10">
                <v-card-title class="headline justify-space-between">
                  <v-btn
                    icon
                    large
                    @click="
                      () => {
                        this.$router.go(-1);
                      }
                    "
                  >
                    <v-icon large>mdi-arrow-left</v-icon>
                  </v-btn>

                  <h2></h2>

                  <v-btn disabled text><v-spacer></v-spacer></v-btn
                ></v-card-title>
                <!--   fields -->
                <v-card-text style="margin:auto;">
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-row>
                      <v-col cols="10">
                        <v-text-field
                          v-model="email"
                          @change="emailcheck = false"
                          :rules="[
                            v => !!v || ' ',
                            v =>
                              /.+@.+\..+/.test(v) || '  ',
                            v => v.length <= 50 || '  '
                          ]"
                          label=""
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="2">
                        <v-btn
                          color="primary"
                          block
                          @click="checkEmail()"
                          :disabled="emailcheck"
                          ></v-btn
                        ></v-col
                      >
                    </v-row>
                    <v-row>
                      <v-col cols="10">
                        <v-text-field
                          v-model="nickname"
                          @change="nicknamecheck = false"
                          :counter="10"
                          :rules="[
                            v => !!v || '  ',
                            v =>
                              (v && v.length > 1 && v.length <= 10) ||
                              ' 2  10   '
                          ]"
                          label=""
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="2">
                        <v-btn
                          color="primary"
                          block
                          @click="checkNickname()"
                          :disabled="nicknamecheck"
                          ></v-btn
                        >
                      </v-col>
                    </v-row>

                    <v-text-field
                      v-model="pw"
                      :rules="[
                        v => !!v || '  ',
                        v =>
                          (v && v.length > 7 && v.length <= 20) ||
                          ' 8  20   '
                      ]"
                      type="password"
                      label=""
                      required
                      class="my-6"
                    ></v-text-field>

                    <v-text-field
                      v-model="pwvaild"
                      :rules="[
                        v => !!v || '  ',
                        v => v === pw || '  '
                      ]"
                      type="password"
                      label=" "
                      required
                      class="my-6"
                    ></v-text-field>

                    <v-row>
                      <v-col>
                        <v-select
                          :items="genders"
                          label=""
                          v-model="gender"
                          :rules="[v => !!v || ' ']"
                          required
                        ></v-select>
                      </v-col>
                      <v-col>
                        <v-text-field
                          label=""
                          placeholder="2021"
                          v-model="birth"
                          :rules="[
                            v => !!v || ' ',
                            v =>
                              (v >= 1900 && v <= 2021) ||
                              '  '
                          ]"
                          required
                        ></v-text-field>
                      </v-col>
                    </v-row>

                    <v-expansion-panels tile class="mt-10">
                      <v-expansion-panel>
                        <v-expansion-panel-header>
                          SPOT 
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                          1 ()<br /><br />
                            SPOT ( '') SPOT
                          ( )   
                          ( )   ( '')
                                  
                           .<br /><br />

                          2 ( )<br /><br />
                                .<br />
                          . ''     
                           ID()  
                          ,      
                           .<br />
                          . 'ID'      
                                 
                              .<br />
                          . ''  ID   
                                
                              .<br />
                          .   ID  
                                  
                            .<br />
                          . ''      
                          .<br />
                          . ID       
                                  
                            .<br />
                               1  
                                  
                          ,     .<br /><br />

                          3 (   )<br />
                          1.       
                            .    
                            .<br />
                          2.       
                                 .
                                
                              , 
                                7 
                          .<br />
                          3.        
                             .<br />
                          4.       
                                . ,
                              2   
                              30    
                               .<br />
                        </v-expansion-panel-content>
                      </v-expansion-panel>
                    </v-expansion-panels>

                    <v-checkbox
                      v-model="check"
                      label=" "
                      class="my-5"
                      :rules="[v => v === true || ' ']"
                    ></v-checkbox>

                    <v-row class="my-1">
                      <v-col
                        ><v-btn
                          block
                          :disabled="!valid"
                          :loading="processing"
                          color="primary"
                          class="mr-4"
                          @click="onSignup"
                        >
                          
                        </v-btn></v-col
                      >
                    </v-row>
                  </v-form>
                </v-card-text>
              </v-card>
            </v-sheet>
          </v-col>
        </v-row>
        <v-snackbar centered v-model="snackbar" timeout="2000" :color="color">
          {{ msg }}
        </v-snackbar>
      </v-container>
      <!--   -->
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-alert border="top" colored-border type="success">
            
          </v-alert>
          <v-card-text>
                 
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="secondary"
              text
              @click="dialog = false"
              router-link
              to="/login"
            >
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-main>
  </v-app>
</template>

<script>
import { mapActions } from "vuex";
const MemberStore = "MemberStore";

export default {
  data() {
    return {
      valid: true,
      email: "",
      emailcheck: false,
      nickname: "",
      nicknamecheck: false,
      pw: "",
      pwvaild: "",
      gender: "",
      genders: ["", ""],
      birth: "",
      check: false,
      processing: false,
      snackbar: false,
      msg: "",
      color: "",
      dialog: false
    };
  },
  methods: {
    ...mapActions(MemberStore, [
      "reqSignup",
      "reqSignupValidation",
      "reqCheckEmail",
      "reqCheckNickname"
    ]),
    reset() {
      (this.email = ""),
        (this.pw = ""),
        (this.pwvaild = ""),
        (this.phone = ""),
        (this.name = "");
    },
    onSignup() {
      // form 
      if (this.$refs.form.validate() === false) return;

      //   
      if (!this.emailcheck) {
        this.msg = "   ";
        this.color = "error";
        this.snackbar = true;
        return;
      }

      //   
      if (!this.nicknameheck) {
        this.msg = "   ";
        this.color = "error";
        this.snackbar = true;
        return;
      }

      this.processing = true;

      this.reqSignup({
        email: this.email,
        pw: this.pw,
        phone: this.phone,
        name: this.name
      })
        .then(response => {
          if (response) {
            this.reqSignupValidation(this.email).then(response => {
              if (response) this.dialog = true;
              else {
                this.msg = "  ";
                this.snackbar = true;
                this.color = "error";
                this.processing = false;
              }
            });
          } else {
            //  
            this.msg = "  ";
            this.snackbar = true;
            this.color = "error";
            this.processing = false;

            this.email = "";
            this.pw = "";
            this.pwvaild = "";
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    checkEmail() {
      this.reqCheckEmail(this.email)
        .then(response => {
          if (response) {
            //   
            this.msg = " ";
            this.snackbar = true;
            this.color = "success";
            this.emailcheck = true;
          } else {
            //  
            this.msg = "  ";
            this.snackbar = true;
            this.color = "error";
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    checkNickname() {
      this.reqCheckNickname(this.nickname)
        .then(response => {
          if (response) {
            //   
            this.msg = " ";
            this.snackbar = true;
            this.color = "success";
            this.emailcheck = true;
          } else {
            //  
            this.msg = "  ";
            this.snackbar = true;
            this.color = "error";
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style></style>
