<template>
  <v-app>
    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col>
            <v-sheet min-height="70vh" rounded="lg">
              <v-card class="pu-10 px-10 mu-10">
                <v-card-title class="headline justify-space-between">
                  <v-btn
                    icon
                    large
                    @click="
                      () => {
                        this.$router.go(-1);
                      }
                    "
                  >
                    <v-icon large>mdi-arrow-left</v-icon>
                  </v-btn>

                  <h2> </h2>

                  <v-btn disabled text><v-spacer></v-spacer></v-btn
                ></v-card-title>
                <v-card-text style="margin: auto;">
                  <v-row>
                    <v-col>
                      <!-- <v-text-field
                        v-model="getMemberInfo.email"
                        disabled
                        label=""
                        solo
                      ></v-text-field> -->
                      <span></span>
                      <p></p>
                      <p
                        style="font-size : 20px; margin-top: 1em; margin-bottom: 1em;"
                      >
                        {{ getMemberInfo.email }}
                      </p>
                    </v-col>
                  </v-row>
                  <v-form ref="form" v-model="NicknameValid" lazy-validation>
                    <v-row>
                      <v-col>
                        <span></span>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="10">
                        <v-text-field
                          solo
                          v-model="nickname"
                          @keydown="nicknamecheck = false"
                          :counter="10"
                          :rules="[
                            v => !!v || '  ',
                            v =>
                              (v && v.length > 1 && v.length <= 10) ||
                              ' 2  10   '
                          ]"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="2">
                        <v-btn
                          color="primary"
                          block
                          @click="checkNickname()"
                          :disabled="nicknamecheck"
                          ></v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-form>
                  <v-form ref="form" v-model="PWValid" lazy-validation>
                    <v-row>
                      <v-col>
                        <span></span>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          v-model="pw"
                          :counter="20"
                          :rules="[
                            v => !!v || '  ',
                            v =>
                              (v && v.length > 7 && v.length <= 20) ||
                              ' 8  20   '
                          ]"
                          type="password"
                          placeholder=" ."
                          solo
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col>
                        <span> </span>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          v-model="pwvaild"
                          :rules="[
                            v => !!v || '  ',
                            v => v === pw || '  '
                          ]"
                          type="password"
                          placeholder="    ."
                          solo
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-form>
                  <!--   -->
                  <v-row justify="end">
                    <v-col cols="auto">
                      <p style="color: #b71c1c;" @click="dialog = true">
                         
                      </p>
                      <v-dialog v-model="dialog" persistent max-width="290">
                        <v-card style="opacity: 1">
                          <v-card-title class="headline">
                            
                          </v-card-title>
                          <v-card-text> ?</v-card-text>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="secondary" text @click="remove()">
                              
                            </v-btn>
                            <v-btn
                              color="secondary"
                              text
                              @click="dialog = false"
                            >
                              
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-col>
                  </v-row>
                  <v-row class="my-1">
                    <v-col
                      ><v-btn
                        block
                        color="primary"
                        class="mr-4"
                        @click="onModify"
                      >
                        
                      </v-btn></v-col
                    >
                    <!-- <v-col cols="4"
                      ><v-btn
                        block
                        color="warning"
                        class="mr-4"
                        @click="
                          () => {
                            this.$router.go(0);
                          }
                        "
                      >
                        
                      </v-btn></v-col
                    > -->
                  </v-row>
                </v-card-text>
              </v-card>
            </v-sheet>
          </v-col>
        </v-row>

        <v-snackbar centered v-model="snackbar" timeout="2000" :color="color">
          {{ msg }}
        </v-snackbar>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
const MemberStore = "MemberStore";

export default {
  data() {
    return {
      NicknameValid: true,
      PWValid: true,
      nicknamecheck: true,
      nickname: "",
      pw: "",
      pwvaild: "",
      snackbar: false,
      color: "",
      msg: "",
      dialog: false
    };
  },
  created() {
    this.reqMemberInfo(this.$route.params.memberid);
    this.nickname = this.getMemberInfo.nickname;
  },
  computed: {
    ...mapGetters(MemberStore, ["getMemberInfo"])
  },
  methods: {
    ...mapActions(MemberStore, [
      "reqMemberInfo",
      "reqCheckNickname",
      "reqModifyMember",
      "reqRemoveMember",
      "Logout"
    ]),
    onModify() {
      if (this.nickname != this.getMemberInfo.nickname) {
        //1.  
        if (!this.nicknamecheck) {
          //2.  
          this.msg = "   ";
          this.color = "error";
          this.snackbar = true;
          return;
        }
        if (this.pw == "" && this.pwvaild == "") {
          //2.    0  
          //  
          this.reqModifyMember({
            nickname: this.nickname,
            pw: "0",
            id: this.$route.params.memberid
          })
            .then(response => {
              if (response) {
                this.msg = " !";
                this.color = "success";
                this.snackbar = true;
                return;
              } else {
                this.msg = "  !";
                this.color = "error";
                this.snackbar = true;
                return;
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          //2.  
          if (!this.PWValid) {
            //3.  
            this.msg = "  ";
            this.color = "error";
            this.snackbar = true;
            return;
          }
          //3. 
          //   
          this.reqModifyMember({
            nickname: this.nickname,
            pw: this.pw,
            id: this.$route.params.memberid
          })
            .then(response => {
              if (response) {
                this.msg = ",  !";
                this.color = "success";
                this.snackbar = true;
                return;
              } else {
                this.msg = ",   !";
                this.color = "error";
                this.snackbar = true;
                this.pw = "";
                this.pwvaild = "";
                return;
              }
            })
            .catch(error => {
              console.log(error);
            });
        }
      } else {
        //1.  
        if (!this.PWValid) {
          //2.  
          this.msg = "  ";
          this.color = "error";
          this.snackbar = true;
          return;
        } else {
          //2.    
          //  
          this.reqModifyMember({
            nickname: this.nickname,
            pw: this.pw,
            id: this.$route.params.memberid
          })
            .then(response => {
              if (response) {
                this.msg = " !";
                this.color = "success";
                this.snackbar = true;
                return;
              } else {
                this.msg = "  !";
                this.color = "error";
                this.snackbar = true;
                this.pw = "";
                this.pwvaild = "";
                return;
              }
            })
            .catch(error => {
              console.log(error);
            });
        }
      }
    },
    checkNickname() {
      //  
      this.reqCheckNickname(this.nickname)
        .then(response => {
          if (response) {
            //   
            this.msg = " ";
            this.snackbar = true;
            this.color = "success";
            this.emailcheck = true;
          } else {
            //  
            this.msg = "  ";
            this.snackbar = true;
            this.color = "error";
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    remove() {
      //
      this.reqRemoveMember(this.$route.params.memberid);
      this.Logout();
      this.$router.push({ name: "main" });
    }
  }
};
</script>

<style></style>
