<template>
  <v-container>
    <v-row justify="space-around">
      <v-col cols="8" align="center">
        <v-text-field
          v-model="msg"
          label=""
          placeholder="  ."
          append-outer-icon="mdi-magnify"
          @keypress.enter="onSearch"
          @click:append-outer="onSearch"
          clear-icon="mdi-eraser"
          clearable
          @click:clear="onReset"
        ></v-text-field>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
const FindStore = "FindStore";

export default {
  data() {
    return {
      msg: ""
    };
  },
  methods: {
    ...mapActions(FindStore, ["reqSearch"]),
    onSearch() {
      //  ,   Store   ?
      this.reqSearch(this.msg); // 
      this.msg = "";
    },
    onReset() {
      this.msg = "";
    }
  }
};
</script>

<style></style>
