import axios from "../../axios/axios-common";

const CourseStore = {
  namespaced: true,
  state: {
    courseList: [],
    courseInfo: [
      {
        course_id: "1",
        spot_id: "1",
        latitude: "37.570425",
        longitude: "126.991316",
        name: "",
        course_name: ""
      },
      {
        course_id: "1",
        spot_id: "2",
        latitude: "37.571525",
        longitude: "126.991316",
        name: " ",
        course_name: ""
      }
    ]
  },
  getters: {
    getCourseList(state) {
      //  
      return state.courseList;
    },
    getCourseInfo(state) {
      //  
      return state.courseInfo;
    }
  },
  mutations: {
    setCourseList(state, payload) {
      state.courseList = payload;
    },
    setCourseInfo(state, payload) {
      state.courseInfo = payload;
    }
  },
  actions: {
    //   
    reqCourseList(context, user_id) {
      return axios
        .get("/courselist/" + user_id)
        .then(response => {
          if (response.data.message == "course list") {
            context.commit("setCourseList", response.data.result);
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //  
    reqCourseInfo(context, fd) {
      return axios
        .get("/course/" + fd.user_id + "?course_id=" + fd.course_id)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "course list info") {
            context.commit("setCourseInfo", response.data.result); //   store .
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    // 
    reqCreateCourse(context, fd) {
      return axios
        .post("/course", {
          spotid: fd.spot_id,
          email: fd.email,
          orders: fd.orders
        })
        .then(response => {
          console.log(response.data);
          if (response.message == "success") {
            return true;
            //context.commit("setCourseInfo", response.data.course);
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 
    reqDeleteCourse(context, course_id) {
      return axios
        .delete("/course/" + course_id)
        .then(response => {
          if (response.message == "success") {
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
          return false;
        });
    }
  }
};

export default CourseStore;
